.table-main{
    background-image: url("./images/bg2.jpg");
    background-repeat: no-repeat;
    background-size:cover;
    background-position:center;
}
.main-content-title {
    font-family: 'Permanent Marker', cursive;
    font-weight: bolder;
    font-size: 60px;
    text-shadow: black 0px 0px 10px;
    width: 90%;
}
button{
    background-color: #E8290B;
    padding:15px;
}

.divider{
    width:100px;
    height:2px;
}

.content-description
{
    width:60%;
    font-family: 'Permanent Marker', cursive;
    font-size: 30px;
    font-weight: lighter;
    text-shadow: black 0px 0px 10px;

}

@media only screen and (max-width: 768px) {
    .main-content-title {
        font-size: 40px;
        text-shadow: black 0px 0px 10px;
        width: 90%;
    }
    .content-description{
        width:60%;
        font-family: 'Permanent Marker', cursive;
        font-size: large;
        text-shadow: black 0px 0px 10px;
    }
 
  }

